.button {
  @include button-reset;
  display: block;
  padding: 14px 0 13px;

  @include font($font-family_secondary, $font-size_s, 700, $line-height_s);
  color: $text-color_secondary;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center;

  border: 1px solid $background-color_primary;
  box-sizing: border-box;
  border-radius: 5px;
  background: none;
  cursor: pointer;


  &_big {
    padding: 18px 28px;

    font-size: $font-size_m;
    line-height: 19px;
  }

  &:hover,
  &:focus {
    background: $background-color_primary;
    color: $text-color_accent;
  }

  &:active {
    opacity: 0.3;
  }
}
