.page-footer {
  position: relative;
  flex-shrink: 0;
  padding-top: 65px;

  @include font($font-family_primary, 15px, 400, 25px);
  color: rgba($text-color_footer, 0.5);
  background: $background-color_footer;

  a {
    color: inherit;
  }

  a:focus,
  a:hover {
    color: $text-color_accent;
  }

  a:active {
    opacity: 0.3;
  }
}

.page-footer__inner {
  position: relative;

  @include container_desktop;
  display: flex;
  flex-wrap: wrap;
}

.page-footer__content {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: calc(50% - 50vw);
    width: 100vw;
    height: 1px;
    background-color: $gray_blueish;
    opacity: 0.2;
  }
}

.page-footer__social {
  margin-right: 20px;
}

.page-footer__logo {
  display: block;
  margin-bottom: 53px;

  &[href]:focus,
  &[href]:hover {
    opacity: 0.7;
  }

  &[href]:active {
    opacity: 0.3;
  }
}

.page-footer__item-list {
  display: flex;
  justify-content: space-between;
  min-width: 70.2%;
}

.page-footer__item {
  margin-bottom: 60px;

  h3 {
    margin: 0 0 25px;

    @include font($font-family_secondary, 15px, 400, $line-height_s);
    color: $text-color_footer;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }

  a {
    display: block;
    font-size: 15px;
    line-height: 25px;
  }
}

.page-footer__item-navigation {
  margin-right: 30px;
}

.page-footer__item-button {
  display: none;
}

.page-footer__navigation-list {
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  min-width: 425px;
  max-height: 100px;
  writing-mode: vertical-lr;

  @include list-reset;

  li {
    white-space: nowrap;
    writing-mode: horizontal-tb;
  }

  li:nth-of-type(n+5) {
    margin-left: 20px;
  }
}

.page-footer__contacts {
  display: flex;
  flex-direction: column;
  margin: 0;
}

.page-footer__info-bar {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 14px 0;
  font-size: 13px;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 0.04em;
}

.page-footer__copyright {
  margin: 0 5% 0 0;
}

.page-footer__years {
  margin: 0 10% 0 0;
}

.page-footer__privacy-link {
  margin-right: auto;
}

@media (max-width: $max-width_tablet) {
  .page-footer {
    padding-top: 0;
  }

  .page-footer__inner {
    @include container_tablet;
  }

  .page-footer__content {
    flex-direction: column;
  }

  .page-footer__social {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 0;
    padding-top: 19px;
    padding-bottom: 25px;
  }

  .page-footer__logo {
    margin-bottom: 0;
  }

  .page-footer__item-list {
    position: relative;
    width: 100%;
    padding: 40px 0;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: calc(50% - 50vw);
      width: 100vw;
      height: 1px;
      background-color: $gray_blueish;
      opacity: 0.2;
    }
  }

  .page-footer__item {
    margin-bottom: 0;
  }

  .page-footer__item-navigation {
    min-width: 50%;
    margin-right: 0;
  }

  .page-footer__contacts {
    padding-right: 12px;
  }

  .page-footer__navigation-list {
    flex-direction: column;
    min-width: 100%;
    max-height: 100%;
    writing-mode: horizontal-tb;

    li:nth-of-type(n+5) {
      margin: 0;
    }
  }

  .page-footer__info-bar {
    padding-top: 20px;
    padding-bottom: 24px;
  }

  .page-footer__copyright {
    display: none;
  }

  .page-footer__years {
    position: absolute;
    top: 36px;
    right: 209px;
    margin-right: 0;
  }
}

@media (max-width: $max-width_mobile) {
  .page-footer__inner {
    @include container_mobile;
  }

  .page-footer__social {
    flex-direction: column;
    padding-top: 46px;
    padding-bottom: 55px;
  }

  .page-footer__logo {
    margin-bottom: 80px;
  }

  .page-footer__item-list {
    flex-direction: column;
    padding: 0;
  }

  .page-footer__item {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: flex-start;
    min-width: 100%;

    h3 {
      min-width: 80%;
      flex-grow: 1;
      margin-bottom: 0;
      padding-top: 30px;
      padding-bottom: 28px;
    }
  }

  .page-footer__item-navigation {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: calc(50% - 50vw);
      width: 100vw;
      height: 1px;
      background-color: $gray_blueish;
      opacity: 0.2;
    }
  }

  .page-footer__contacts,
  .page-footer__navigation {
    padding-bottom: 27px;
  }

  .page-footer__years {
    width: 100%;
    top: 124px;
    right: 0;
    text-align: center;
  }

  .page-footer__info-bar {
    flex-direction: column;
    padding-top: 28px;
    padding-bottom: 26px;
  }

  .page-footer__privacy-link {
    margin-right: 0;
    margin-bottom: 21px;
  }
}
